body {
  font-family: sans-serif;
  margin: 0 auto;
  padding: 25px;
  background-color: #dfe6e9;
  text-align: center;
}

img{
  border-radius: 70px;
  height:150px;
  width:150px;
}

li {
  display:inline; 
  list-style: none;
}
 

.heading{
  background-color:#81eccc;
  margin: 20px;
  width:6000px;
  display:flex;
  flex-direction: row;
  justify-content: space-around;
}

.card {
  border-radius: 25px;
  padding: 25px 15px;
  background-color: #81eccc;
  margin: 50px 50px 100px 300px;
  height: 300px;
  width:200px;
  box-shadow: 0 2px 5px #ccc;
  text-align: center;
 
  
  
}

.parent {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  
}



@media only screen and (min-width:320px) and(max-width:480px){

  
.child{
  margin-left:100px;

  
  }
}
.card {
  border-radius: 20px;
  padding: 20px 15px;
  background-color: #81eccc;
  margin: 50px 50px 0px 35px;
  height: 400px;
  width:100%;
  box-shadow: 0 2px 5px #ccc;
  text-align: left;
}